@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Rock+Salt&family=Spectral:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* For mobile */
.link a {
    text-decoration: none;
    margin-left: 10px;
    font-size: 50px;
    color: white;
    font-family: 'Courier New', Courier, monospace;
}

.iconColor {
    color: white;
}

.listItem {
    margin: 0;
    padding: 0px 20px;
    border: 0;
}

.navBar {
    background: #49494b;
    min-height: 80px !important;
}

.expandNav {
    color: white;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px 20px !important;
}

/* For desktop */
@media (min-width: 1000px) {
    .link a {
        font-size: 160%;
    }

    .navBar {
        min-height: unset;
    }
    
    .expandNav {
        margin-top: unset;
        margin-bottom: unset;
        padding: 10px 10px !important;
    }
}
a:hover {
    color: #bd8c7d;
}

.footer {
    font-size: 20px;
}
/* For mobile devices */
.firstColumn {
    margin-left: 100px;
    margin-right: 100px;
    max-width: 800px;
    margin-bottom: 50px;
}

.secondColumn * {
    display: none;
}

.hiddenColumn * {
    display: none;
}

.landscapeImg {
    width: 100%;
    max-width: 750px;
    height: auto;
    margin-bottom: 20px;
    margin-top: 20px;
}

.title {
    margin-top: 150px;
    font-size: 100px;
    font-weight: 500;
    letter-spacing: 12px;
    text-align: center;
    font-family: 'Spectral', serif;
    color: #bd8c7d;
}

.subtitle {
    font-size: 40px;
    font-family: 'Courier New', Courier, monospace;
    flex-wrap: wrap;
    text-align: center;
}

.p {
    font-family: 'Courier New', Courier, monospace; 
    flex-wrap: wrap; 
    font-size: 30px;
    align-content: center;
    margin-top: 20px;
}

.linkGroup a {
    text-decoration: none;
    border-bottom: 1px solid;
    color: #bd8c7d;
    font-size: 30px;
    font-family: 'Courier New', Courier, monospace;
}

/* For desktop devices */
@media (min-width: 1000px) {
    .firstColumn {
        margin-left: 150px;
        margin-right: 0px;
    }

    .secondColumn {
        display: unset;
        height: 230px;
        width: 200px;
        margin-left: 50px;
        margin-right: 0px;
    }

    .flowerImg{
        position: relative;
        margin-top: 400px;
        height: 230px;
        width: 200px;
    }

    .portfolioImg {
        display: unset;
        object-fit: cover;
        margin-top: 20px;
        width: 450px;
        height: 730px;
    }

    .boxLg {
        position: absolute;
        height: 230px;
        width: 200px;
        margin-top: 140px;
        background-color: #bd8c7d;
    }

    .title {
        font-size: 80px;
        margin-top: 100px;
        letter-spacing: 2.5rem;
        text-align: left;

    }

    .subtitle {
        font-size: 30px;
        text-align: left;
    }
    
    .p {
        font-size: 15px;
        margin-bottom: 30px;
    }
    
    .linkGroup a {
        font-size: 20px;
    }

}

@media (min-width: 1500px) {
    .hiddenColumn * {
        display: inline;
        margin-right: 40px;
    }
}
.container {
    margin-top: 0px;
    display: flex;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
